/* custom accordion */
.accordion-custom {

  @include screen-pc {
    display: none;
  }
}

#nav-drawer {
  position: relative;
}

/**/
.nav-unshown {
  display:none;
}

/**/
#nav-open {
  display: inline-block;
  width: 100px;
  height: 90px;
  vertical-align: middle;
  cursor: pointer;
  // border: 1px solid #ddd;
  padding: 28px 30px;
}

/*CSS*/
#nav-open span, #nav-open span:before, #nav-open span:after {
  position: absolute;
  height: 4px;/**/
  width: 40px;/**/
  border-radius: 0;
  background: $secondary-color;
  display: block;
  content: '';

}
#nav-open span:before {
  bottom: -13px;
}
#nav-open span:after {
  bottom: -26px;
}

/**/
#nav-close {
    display: none;/**/
    position: fixed;
    z-index: 99;
    top: 0;/**/
    left: 0;
    width: 100%;
    height: 100%;
    background: $primary-color;
    opacity: .5;
    transition: .3s ease-in-out;
}

/**/
#nav-content {
    overflow: auto;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;/**/
    width: 90%;/**/
    max-width: 330px;/**/
    height: 100%;
    background: #fff;/**/
    transition: .3s ease-in-out;/**/
    -webkit-transform: translateX(-105%);
    transform: translateX(-105%);/**/
}

/**/
#nav-input:checked ~ #nav-close {
    display: block;/**/
    opacity: .5;
}

#nav-input:checked ~ #nav-content {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);/**/
    /* box-shadow: 6px 0 25px rgba(0,0,0,.15); */
}
